import Vue from 'vue'
import Vuex from 'vuex'
// Âú®Êï∞ÁªÑ‰∏≠Êü•Êâæ ÊâÄÊúâÂá∫Áé∞ÁöÑxÔºåÂπ∂ËøîÂõû‰∏Ä‰∏™ÂåÖÂê´ÂåπÈÖçÁ¥¢ÂºïÁöÑÊï∞ÁªÑ
function findall (a, x) {
  var results = []
  var len = a.length
  var pos = 0
  while (pos < len) {
    pos = a.indexOf(x, pos)
    if (pos === -1) { // Êú™ÊâæÂà∞Â∞±ÈÄÄÂá∫Âæ™ÁéØÂÆåÊàêÊêúÁ¥¢
      break
    }
    results.push(pos) // ÊâæÂà∞Â∞±Â≠òÂÇ®Á¥¢Âºï
    pos += 1 // Âπ∂‰ªé‰∏ã‰∏™‰ΩçÁΩÆÂºÄÂßãÊêúÁ¥¢
  }
  return results
}
// ÂºïÁî®Á±ªÂûãÊ∑±Êã∑Ë¥ù
function deepCopy (s) {
  const d = []
  for (let k in s) {
    if (typeof s[k] === 'object') {
      d[k] = deepCopy(s[k])
    } else {
      d[k] = s[k]
    }
  }
  return d
}
// ES6Êï∞ÁªÑÂéªÈáç
function unique (arr) {
  return Array.from(new Set(arr))
}

Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    count: 0,
    click_item_num: 0, // ÊöÇÂ≠òÂΩìÂâçÁÇπÂáªÊù°ÁõÆÁöÑ‰∏ãÊ†á
    status: false, // ËÆ∞ÂΩïÂΩìÂâçÁöÑÁôªÂΩïÁä∂ÊÄÅ
    list: [],
    note_username: '',
    note_password: ''
  },
  mutations: {
    upp () {
      // this.state.count++
      store.state.count++
    },
    clearAll () { // ÂÖàÊ∏ÖÈô§storeÔºåÂÜçÊ∏ÖÈô§localStorageÔºÅÔºàÂê¶ÂàôÂç≥‰ΩøÂÖàÊ∏ÖÈô§storageÔºåstore‰∏≠ÁöÑÊï∞ÊçÆ‰πü‰ºöÂõ†È°µÈù¢Âà∑Êñ∞ÊàñÂÆöÊó∂‰øùÂ≠òÔºåÂÜçÊ¨°Ë¢´Â≠òÂÖ•storageÔºâ
      store.state.count = 0
      store.state.list = []
      store.state.click_item_num = 0
      localStorage.removeItem('store')
      console.log(store.state.list)
    },
    register1 (state, value) {
      store.state.note_username = value.regname
      store.state.note_password = value.regpass
    },
    changeStatus (state) {
      state.status = !state.status
    },
    addItem (state, value) {
      state.list.unshift(value)
    },
    updateItem (state, value) {
      state.list[state.click_item_num] = value
    },
    sortList (state) {
      /* Êù°ÁõÆÂä®ÊÄÅ‰ª•Êó∂Èó¥ÈôçÂ∫èÈáçÊéí */
      var timeList = []
      for (let i = 0; i < state.list.length; i++) {
        (function (ind) {
          timeList.push({'cur_time': state.list[ind].note_time.curDate, 'item_index': ind})
        }(i))
      }

      var curTime = [] // ÂáÜÂ§áÂ≠òÊîæ ÈáçÊéíÂ∫èÂâç ÂêÑ‰∏™Êù°ÁõÆÁöÑÁÆÄÂåñÊó∂Èó¥ÔºåÁ±ª‰ºº["201912210129", "201912210132", "201912202032", "201912202030"]
      for (let i = 0; i < timeList.length; i++) {
        curTime.push(timeList[i].cur_time)
      }
      var _curTime = deepCopy(curTime) // ÂáÜÂ§áÂØπcurTimeÊï∞ÁªÑÈáçÊéíÂ∫èÔºåÂéüÊï∞ÁªÑ_curTimeÂÖÉÁ¥†È°∫Â∫è ‰∏çÂèóÂΩ±Âìç

      // console.log(curTime) // ÁÆÄÂåñÊó∂Èó¥Êï∞ÁªÑÈõÜÂêà
      curTime = unique(curTime) // ÁÆÄÂåñÊó∂Èó¥Êï∞ÁªÑ‰∏≠ ÂÖÉÁ¥†ÂéªÈáç„ÄÇ["201912210129", "201912210132", "201912202030"]

      for (let i = 0; i < curTime.length - 1; i++) { // Â∑≤ÂéªÈáçÁöÑÁÆÄÂåñÊó∂Èó¥Êï∞ÁªÑ ÈôçÂ∫è„ÄÇ["201912210132", "201912210130", "201912210129"]
        for (let j = 0; j < curTime.length - 1 - i; j++) {
          if (curTime[j] < curTime[j + 1]) {
            var ls = curTime[j]
            curTime[j] = curTime[j + 1]
            curTime[j + 1] = ls
          }
        }
      }
      // console.log(curTime) // ÂéªÈáçÔºåÈôçÂ∫è ÁÆÄÂåñÊó∂Èó¥Êï∞ÁªÑÈõÜÂêà

      // ‰æùÊ¨°ÈÅçÂéÜÊï∞ÁªÑcurTimeÔºàÈôçÂ∫è‰∏îÂÖÉÁ¥†ÂîØ‰∏ÄÔºâ‰∏≠ÁöÑÁõÆÊ†áÂÖÉÁ¥†ÔºåÊü•ÊâæÂú®_curTimeÂéüÊï∞ÁªÑÔºàÂèØËÉΩÊúâÈáçÂ§çÂÖÉÁ¥†Ôºâ‰∏≠ÂåÖÂê´ËØ•ÁõÆÊ†áÂÖÉÁ¥†ÁöÑ ÊâÄÊúâÁ¥¢Âºï
      var itemIndex = ''
      for (let i = 0; i < curTime.length; i++) {
        (function (ind) {
          itemIndex += findall(_curTime, curTime[ind]) // ÂØπÊØèÊ¨°ÈÅçÂéÜ‰∏çÂêåÁöÑÁõÆÊ†áÂÖÉÁ¥† ËøîÂõûÁöÑÁ¥¢ÂºïÊï∞ÁªÑ Áõ¥Êé•ËøõË°åÂ≠óÁ¨¶‰∏≤ÊãºÊé•„ÄÇËøôÈáåÊòØ'1,230'
        }(i))
      }

      itemIndex = itemIndex.replace(',', '').split('') // ÂÖàÂéªÈô§Â≠óÁ¨¶‰∏≤‰∏≠ÁöÑ ÈÄóÂè∑ÔºåÂÜçÂàÜÂâ≤Â≠óÁ¨¶‰∏≤‰∏∫Êï∞ÁªÑ
      // console.log(itemIndex) // ÈáçÊéíÂ∫èÂêéÁöÑÊï∞ÁªÑÂÖÉÁ¥† Âú®ÊóßÊï∞ÁªÑ‰∏≠ ÂØπÂ∫îÁöÑ Á¥¢ÂºïÈõÜÂêà„ÄÇËøôÈáåÊòØ[1,2,3,0]

      // listÊù°ÁõÆÊï∞ÁªÑ‰ª• ÂèëÂ∏ÉÊó∂Èó¥ÈôçÂ∫è ÈáçÊéíÂ∫è
      var listNew = []
      for (let i = 0; i < itemIndex.length; i++) {
        listNew.push(state.list[itemIndex[i]])
      }
      state.list = listNew
    }
  },
  actions: {
  }
})
export default store



